* {
    box-sizing: border-box;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #fafafa;
    color: #212121;
    line-height: 1.5;
}

img {
    display: block;
    max-width: 100%;
}

.button {
    cursor: pointer;
    height: 44px;
    min-width: 64px;
    border: none;
    border-radius: 10px;
    font-size: 16px;
    line-height: 1.29;
    padding: 0 20px;
    margin-right: 10px;
}

.first {
    background-color: rgb(65, 32, 252);
}

.second {
    background-color: yellow;
}

.timer,
.switcher {
    margin-top: 30px;
    display: flex;
    justify-content: center;
}

.field {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
    width: 70px;
    height: 70px;
    font-size: 35px;
    font-weight: 700;
    color: darkgreen;
    text-shadow: 2px 2px 6px rgb(38 75 38);
    flex-direction: column;
    text-transform: uppercase;
}

.label {
    font-size: 15px;
    font-weight: 600;
    color: rgb(2, 46, 2);
    /* box-shadow: 5px 2px 10px rgb(38 75 38); */
}

.input {
    display: inline-block;
    padding: 10px;
    border-radius: 5px;
}

.timer-start {
    font-weight: 600;
    font-size: 20px;
    color: #fafafa;
    text-shadow: 3px 3px 3px #000000;
    background: linear-gradient(to top, yellow 40%, blue 60%);
}